import { useEffect } from "react";
import { NavLink } from "react-router-dom";
const $ = window.$;
const AuditNav = ({props}) => {
  useEffect(()=>{
    // control the arrow symbol for collapse
    $(".collapse").on('show.bs.collapse', function(){
        $(this).parent().children(':first-child').addClass('collapse-open');
    });
    $(".collapse").on('hide.bs.collapse', function(e){
        $(e.target).parent().children(':first-child').removeClass('collapse-open');
    });
  },[props]);

  if(!props.hasOwnProperty('audit'))
  return <></>;
  return props.hasOwnProperty('audit') && ( 
  <li className="nav-item">
    <a href="/#" className="nav-link" data-toggle="collapse" data-target="#sidenav-collapse-0">
    <i className="nav-icon fas fa-th"></i>
    <p>
        Audit
        <i className="fas fa-angle-left right"></i>
    </p>
    </a>
    <ul className="nav collapse" id="sidenav-collapse-0">
        <li className="nav-item">
            <NavLink exact to="/main-audit" className="nav-link">
            <i className="far fa-circle nav-icon"></i>
            <p>Main Audit</p>
            </NavLink>
        </li>
        {
          props.audit.SAQ &&
          (<li className="nav-item sub">
                <a href="#/" className="nav-link" data-toggle="collapse" data-target="#AUDIT_SAQ">
                    <i className="far fa-circle nav-icon"></i>
                    <p>SAQ
                    <i className="fas fa-angle-left right"></i>
                    </p>
                </a>
                <ul className="nav collapse" id="AUDIT_SAQ">
                    {
                        props.audit.SAQ.map(saq =>(
                            <li className="nav-item" key={saq.path}>
                                <NavLink exact to={saq.path} className="nav-link">
                                <i className="far fa-dot-circle nav-icon"></i>
                                <p>{saq.title || ''}</p>
                                </NavLink>
                            </li>
                        ))
                    }
                </ul>
            </li>)
        }
        
    </ul>
  </li>
 );
}
 
export default AuditNav;